import React from 'react'
import {Link} from 'react-router-dom'
import classes from './NavItems.module.css';
import {useStateValue} from '../../../../../container/StateProvider'
import {auth} from '../../../../../container/Server/Firebase'

function NavbarItems(props) {
const[{login},dispatch]=useStateValue()
let logOut =null;

const logoutHandler=()=>{
auth.signOut().then(function() {
        alert('logOut success full')
        dispatch({
            type:'LOG_IN',
            item:{
                state:false,

            },
        })
      }, function(error) {
        alert(error)
      });
}

if(login.state === true){
    logOut=(
        <>
        <Link className={classes.Items} to='./seller' onClick={props.click} exject >Sell</Link>
        <Link className={classes.Items} to="./" onClick={logoutHandler} exject >Log Out</Link>  
        </>
        )
}
else{
    logOut=(
        <Link className={classes.Items} to="./sell"  exject >SignIn</Link>  
    )
}


    return (
        <div className={classes.NavbarItems}>
            <Link className={classes.Items} to='./' onClick={props.click} exject >Home</Link>
      <Link className={classes.Items} to="./About" onClick={props.click} exject >About Us</Link>
{logOut}    
        </div>
    )
}

export default NavbarItems
