import React,{useState} from 'react'
import {useStateValue} from '../../../../container/StateProvider'
import axios from '../../../../container/Server/axios'
import Logo from '../../../Logo/Logo'
import classes from './About.module.css'
import {storage} from '../../../../container/Server/Firebase'
import { ToastContainer, toast } from 'react-toastify';
import SimpleDropZone from './DropZone/DropeZone'

function About() {
const [state, setstate] = useState({selectedFile:null})
const [url, setUrl] = useState('')
const [name, setname] = useState('')
const [phone, setphone] = useState('')
const [description, setdescription] = useState('')
const [price, setprice] = useState('')
const [quality, setquality] = useState('')
const [location, setlocation] = useState('')
const [type, settype] = useState('')
const [{basket},dispatch]=useStateValue();


//uploading an image to the storage

const onChangeHandler=(e)=>{
    setstate({
        selectedFile:e.target.files[0]
    })
    
    }
    

    
    async function fileUploadHandler(){

        const uploadTask= storage.ref(`images/${state.selectedFile.name}`).put(state.selectedFile);
        uploadTask.on(
        "state_changed",
        snapshot =>{},
        error=>{
            
            toast(error,{type:'error'})
        },
        ()=>{
            storage
            .ref('images')
            .child(state.selectedFile.name)
            .getDownloadURL()
            .then(url =>{
                setUrl(url)
                toast('Image Uploaded',{type:"info"})
            })
        }
    )
    
    }
    


    const onFormSubmit=(e)=>{

        e.preventDefault()
        console.log(name,phone,location,type,price)
         const order=  { 
            name,
            phone,
            price,
            description,
            imageUrl:url,
            type,
            quality:e.target.elements.quality.value,
            location
        }
console.log(order.type)
if(order.imageUrl !== null){
    axios.post(`${order.type}.json`, order)

        .then(e=>{
    return(        
    toast('Successfully Added Your Product'),{type:"success"},
    setname(''),
    setphone(''),
    setprice(''),
    settype(''),
    setlocation(''),
    setdescription(''))
    })
        .catch(e=>alert(e))

}
else{
    toast('Image is Uploading...',{type:"error"})
}
    
        //Add item to the basket
dispatch({
    type:"ALL",
    item: { 
        name,
        phone,
        price,
        description,
        imageUrl:url,
        type,
        quality:e.target.elements.quality.value,
        location
    }
})

    }

    return (
        <>
    <form onSubmit={onFormSubmit}>
  <strong>Product Name ðŸ‘‡</strong>   <input type='text' value={name} onChange={(e)=>setname(e.target.value)}  required name='product' placeholder="Enter your product name"  />
    <strong>Enter Your Verified Phone Number ðŸ‘‡</strong><input  type="number" value={phone} onChange={(e)=>setphone(e.target.value)}  required name='phone' placeholder="Enter Phone Number" />
     <strong>Product Image ðŸ‘‡ </strong><input type="file" onChange={onChangeHandler} /> <button onClick={fileUploadHandler}>Upload</button>
     {/* <SimpleDropZone /> */}
        <strong>Enter Price ðŸ‘‡</strong> <input  type="number" value={price} onChange={(e)=>setprice(e.target.value)}  required name='price' placeholder="Enter Price" />
    <strong>Product Description ðŸ‘‡</strong><textarea name="textarea" value={description} onChange={(e)=>setdescription(e.target.value)} />
    <strong>Product Quality ðŸ‘‡</strong>
<div style={{display:'flex'}}> 
   <input style={{margin:'5px 10px 5px 0 '}} type="radio" value="Excellent" name='quality'   /> Excellent
    <input style={{margin:'5px 10px'}}  name='quality' type="radio"  value="Good" /> Good 
    <input style={{margin:'5px 10px'}} name='quality' type="radio"  value="Medium" /> Medium 
    <input style={{margin:'5px 10px'}} name='quality' type="radio" value="Bad" /> Low
    
</div>
<strong>Product Location ðŸ‘‡</strong>   <input type='text' value={location} onChange={(e)=>setlocation(e.target.value)} required name='location' placeholder="Enter your product location"  />
    <strong>Select Your Product Type ðŸ‘‡</strong>
    <select value={type} onChange={(e)=>settype(e.target.value)} name="select">
        <option value="Electronic">Electronic</option>
        <option value="Assories">Assories</option>
        <option value="Hardware">Hardware</option>
        <option value="Grossery">Grossery</option>
        <option value="Furniture">Furniture</option>
        <option value='Others'>Others</option>
    </select><br />
    <button>Submit</button>
</form>

        </>
    )
}

export default About


